import React from 'react';
import { fnOutput } from '../store/function';

const CompHourData = ({hourData}) => {
  let {month, date, hour, icon, rain, snow} = fnOutput(hourData)
  
  return (
    <li>
      <div>
        <b>{month}월 {date}일</b>
        <b>{hour}시</b>
        <img src={`${process.env.PUBLIC_URL}/img/icons/${icon}.gif`} alt="" />
        <b>
          <i className="fa-solid fa-cloud-rain"></i>
          {rain}mm
        </b>
        <b>
          <i className="fa-regular fa-snowflake"></i>
          {snow}mm
        </b>
      </div>
    </li>
  );
};

export default CompHourData;