import React, { useContext } from 'react';
import { AppContext } from '../App';
import { fnOutput } from '../store/function';

const CompDetail = () => {
  let {stWeather} = useContext(AppContext)
  if(!stWeather) return false
  let {icon, feellike, clouds, uvi, humidity, pressure,visibility} = fnOutput(stWeather.current)


  return (
    <section className='detail_section'>
      <div className="inner">
        <h2>
          <img className='icon' src={`${process.env.PUBLIC_URL}/img/icons/${icon}.gif`} alt="" />
          <span>Detail Forecast</span>
        </h2>                
        <ul>
          <li>
            <h3><i className="fa-solid fa-temperature-three-quarters"></i>체감온도</h3>
            <p><b>{feellike}</b><sup>&#8451;</sup></p>
          </li>
          <li>
            <h3><i className="fa-solid fa-cloud"></i>구름양</h3>
            <p><b>{clouds}</b><small>%</small></p>
          </li>
          <li>
            <h3><i className="fa-solid fa-cloud-sun"></i>자외선수치</h3>
            <p>
              <b>{uvi}</b>
            </p>
            <q>
                낮음:2이하, 보통: 3-5, 높음:6-7, 매우 높음:8-10, 위험:11이상
            </q>
          </li>
          <li>
            <h3><i className="fa-solid fa-droplet"></i>습도</h3>
            <p><b>{humidity}</b><small>%</small></p>
          </li>
          <li>
            <h3><i className="fa-solid fa-hurricane"></i>기압</h3>
            <p><b>{pressure}</b><small>hPa</small></p>
          </li>
          <li>
            <h3><i className="fa-solid fa-eye-low-vision"></i>가시성</h3>
            <p><b>{visibility}</b><small>km</small></p>
          </li>
        </ul>
      </div>
    </section>
  );
};

export default CompDetail;