import React from 'react';
import { fnOutput } from '../store/function';

const CompDatilyData = ({dailyData}) => {
  let {month, date, day, icon, rain, snow, tempMin, tempMax} = fnOutput(dailyData)
  return (
    <li>
        <b>{month}월 {date}일</b>
        <b>{day}</b>
        <img src={`${process.env.PUBLIC_URL}/img/icons/${icon}.gif`} alt="" />
        <b>
          <i className="fa-solid fa-temperature-arrow-up"></i>
          {tempMax} <sup>&#176;</sup>
        </b>
        <b>
          <i className="fa-solid fa-temperature-arrow-down"></i>
          {tempMin} <sup>&#176;</sup>
        </b>
        <b>
          <i className="fa-solid fa-cloud-rain"></i>
          {rain}mm
        </b>
        <b>
          <i className="fa-regular fa-snowflake"></i>
          {snow}mm
        </b>
    </li>
  );
};

export default CompDatilyData;