import { codeEnArr } from "./conditionCode"
import { codeKrArr } from "./conditionCode"

/* 현재 위치 정보를 가져와서 위경도를 반환하는 함수 */
export const fnGetCurrentLocation = () => {
  return new Promise(resolve => {
    navigator.geolocation.getCurrentPosition(location => {
      let latLng = {
        lat: location.coords.latitude,//위도
        lng: location.coords.longitude //경도
      }
      resolve(latLng) // return과 같은 의미이기 때문에, 이 아래로는 작성하지 말것
    })//getCurrentPosition
  })//promise
}
/* 첫번째 함수 끝 */

/* 위경도를 받아서 주소를 반환하는 비동기 함수 */
export const fnGetAddress = (lat, lng) =>{
  return new Promise(resolve=>{
    let geocoder = new window.google.maps.Geocoder;
    let latLng = {
      lat, lng
    };
    geocoder.geocode({'location': latLng }, function (address, status) {
      if (status === "OK") {
        resolve(address)
      } else {
        resolve(null)
      }
    }); 
  })//promise
}
/* 두번째 함수 끝 */

/* 위경도를 받아서 날찌 정보를 반환하는 비동기 함수 */
export const fnGetWeather = (lat, lng) =>{
  return new Promise(resolve => {
    fetch(`https://api.openweathermap.org/data/3.0/onecall?lat=${lat}&lon=${lng}&appid=5055241fcbceeccb5610082aceaee797`).then(result=>{
      let data = result.json()

      resolve(data) // json() 명령어는 비동기에서만 사용가능,  JSON.parse('로컬스토리지에서 가져온 데이터'):동기에서 사용
    }).catch(()=>{
      alert('날씨정보를 받아오지 못했습니다. 잠시 후 다시 시도해 주세요')
      resolve(null)
    })
  })//promise
}
/* 세번째 함수 끝 */


export const fnOutput = (category) => {
  let temp = (category.temp - 273.15).toFixed(1)
  let time = new Date(category.dt * 1000)
  let year = time.getFullYear()
  let month = time.getMonth() + 1
  let date = time.getDate()
  let hour = time.getHours()
  let minute = time.getMinutes()
  minute = (minute<10)?'0'+minute:minute
  let dayArr = ['SUN', 'MON', 'TUE', 'WED', 'THU', 'FRI', 'SAT']
  let day = dayArr[time.getDay()]
  let desc = codeKrArr[codeEnArr.indexOf(category.weather[0].description)]
  let windDeg = category.wind_deg
  let windSpeed = category.wind_speed
  let rain = (category.rain)? category.rain : 0 //시간별정보에서는 객채로 변환함
  rain = (typeof(rain)==='object')? rain['1h']:rain
  let snow = (category.snow)? category.snow : 0 
  snow = (typeof(snow)==='object')? snow['1h']:snow
  let sunrise = (category.sunrise) && new Date(category.sunrise * 1000)
  let sunriseHour = (sunrise) && sunrise.getHours()
  let sunriseMin = (sunrise) && sunrise.getMinutes()
  let sunset = (category.sunset)&& new Date(category.sunset * 1000)
  let sunsetHour = (sunset)&& sunset.getHours()
  let sunsetMin = (sunset)&& sunset.getMinutes()
  let tempMin = typeof(category.temp === 'object')&&(category.temp.min - 273.15 ).toFixed(1)
  let tempMax = typeof(category.temp === 'object')&&(category.temp.max - 273.15 ).toFixed(1)
  let icon = category.weather[0].icon
  let feellike = (category.feels_like - 273.15).toFixed(1)
  let clouds = category.clouds
  let uvi = category.uvi
  let humidity = category.humidity
  let pressure = category.pressure
  let visibility = (category.visibility/1000).toFixed(1)
  return ({
    temp, year, month, date, hour, minute, day, desc, windDeg, windSpeed, rain, snow,
    sunriseHour, sunriseMin, sunsetHour, sunsetMin, tempMin, tempMax, icon, 
    feellike, clouds, uvi, humidity, pressure, visibility
  })
}