import React, { useContext } from 'react';
import { AppContext } from '../App';
import { fnOutput } from '../store/function';

const CompCurrent = () => {
  let { stAddress, stWeather } = useContext(AppContext)
  if (stAddress === null || stWeather === null) return false
  let current = stWeather.current
  let today = stWeather.daily[0]
  let data = fnOutput(current)
  let todayData = fnOutput(today)
  // 반원에서 해 이동 각도 구하기
  let nowMin = parseInt(data.hour) * 60 + parseInt(data.minute)
  let sunriseMinute = parseInt(todayData.sunriseHour) * 60 + parseInt(todayData.sunriseMin)
  let sunsetMinute = parseInt(todayData.sunsetHour) * 60 + parseInt(todayData.sunsetMin)
  let deg = (nowMin - sunriseMinute) / (sunsetMinute - sunriseMinute) * 180
  if(deg<0) deg=0
  if(deg>180) deg=180
  // 주소 가져오기
  let address
  try{
    address = stAddress[2].formatted_address
  } catch{
    address = '확인되지 않은 주소 입니다'
  }
  // detail 페이지 열리는 함수
  const fnOpenDetail=(e)=>{
    e.currentTarget.classList.toggle('active')
    document.querySelector('.detail_section').classList.toggle('active')
  }
  return (
    <section className='current_section'>
      <div className='inner'>
        <p className='address'>
          {address}
        </p>
        <div className='current_info'>
          <div className='circle_wrap'>
            <div className='sun_time'>
              <div className='sun_circle'></div>
              <time className='sunrise'>
                <i className="fa-solid fa-sun"></i>
                {todayData.sunriseHour}:{todayData.sunriseMin}
              </time>
              <time className='sunset'>
                <i className="fa-solid fa-moon"></i>
                {todayData.sunsetHour}:{todayData.sunsetMin}
              </time>
              <div className='sun' style={{ transform: `rotate(${deg}deg)` }}>
                <i className="fa-solid fa-sun"></i>
              </div>
            </div>

            <p className='temp_min_max'>
              <b>{todayData.tempMin}<sup>&#176;</sup></b>
              <span>/</span>
              <b>{todayData.tempMax}<sup>&#176;</sup></b>
            </p>

            <p className='temp'>
              <img src={`${process.env.PUBLIC_URL}/img/icons/${data.icon}.gif`} alt="" />
              <span><b>{data.temp}</b><sup>&#8451;</sup></span>
            </p>

            <p className='desc'>
              "{data.desc}"
            </p>

            <p className='rain_snow'>
              <b className='rain'>
                <i className="fa-solid fa-umbrella"></i>
                {data.rain} <small>mm/h</small>
              </b>
              <b className='snow'>
                <i className="fa-regular fa-snowflake"></i>
                {data.snow} <small>mm/h</small>
              </b>
            </p>

            <p className='wind'>
              <b className='wind_deg'>
                <i className="fa-solid fa-location-arrow" style={{ transform: `rotate(${data.windDeg - 45}deg)` }}></i>
              </b>
              <b className='wind_speed'>
                <i className="fa-solid fa-wind"></i>
                {data.windSpeed} <small>m/sec</small>
              </b>
            </p>


          </div>
        </div>
        <p className='time'>
          <time className='current_time'>
            <b>{(data.hour <= 12) ? data.hour : data.hour - 12} : {data.minute} </b>
            <small>{(data.hour <= 12) ? 'AM' : 'PM'}</small>
          </time>
          <time>
            <b>{data.year}년 {data.month}월 {data.date}일</b>
            <small>({data.day})</small>
          </time>
        </p>
      </div>
      <button className='more' onClick={fnOpenDetail}>
        <i className="fa-solid fa-circle-plus plus"></i>
        <i className="fa-solid fa-circle-minus minus"></i>
      </button>
    </section>
  );
};

export default CompCurrent;