import React from 'react';
import { useContext } from 'react';
import { AppContext } from '../App';
import CompDatilyData from './CompDatilyData';

const CompDaily = () => {
  let {stWeather} = useContext(AppContext)
  if(!stWeather) return false
  let dailyArr = stWeather.daily
  return (
    <section className='daily_section'>
      <div className='inner scr_style'>
        <h2>
          <i className="fa-solid fa-calendar-days"></i>
          <span>Daily Forecast</span>
        </h2>
        <div className="daily_container">
          <ul>
            {
              dailyArr.map((v, i) => {
                if (i <= 7) {
                  return <CompDatilyData dailyData={v} key={i}/>
                }
              })//map
            }
          </ul>
        </div>
      </div>
    </section>
  );
};

export default CompDaily;