import { createContext, useEffect, useState } from "react";
import CompCurrent from "./Comp/CompCurrent";
import CompDaily from "./Comp/CompDaily";
import CompDetail from "./Comp/CompDetail";
import CompHeader from "./Comp/CompHeader";
import CompHour from "./Comp/CompHour";
import CompMap from "./Comp/CompMap";
import { fnGetCurrentLocation } from "./store/function";
import { fnGetAddress } from "./store/function";
import { fnGetWeather } from "./store/function";
export const AppContext = createContext()

const App = () => {
  let [stLatLng, stChangeLatLng] = useState(null) //위도와 경도 정보
  let [stAddress, stChangeAddress] = useState(null) // 주소 정보
  let [stWeather, stChangeWeather] = useState(null) // 날씨 정보

  const fnInit = async () => {
    let { lat, lng } = await fnGetCurrentLocation() //위경도를 객채로 반환
    let address = await fnGetAddress(lat, lng) //위경도를 받아서 주소를 반환
    let weather = await fnGetWeather(lat, lng) //위경도를 받아서 날씨정보를 반환
    let bg = weather.current.weather[0].main
    document.getElementById('root').style.backgroundImage = `url(${process.env.PUBLIC_URL}/img/weather/${bg}.jpg)`
    stChangeLatLng({ lat, lng })
    stChangeAddress(address)
    stChangeWeather(weather)

    console.log(lat, lng);
    console.log(address);
    console.log(weather);
  } //promise 로 만들고 async로 구현함

  useEffect(() => {
    fnInit()

  }, []) //useEffect

  return (
    <AppContext.Provider value={{
      stLatLng, stChangeLatLng,
      stAddress, stChangeAddress,
      stWeather, stChangeWeather,
      fnInit
    }}>
      <CompHeader />              
      <main className="app_container scr_style">
        <CompCurrent />
        <CompDetail />
        <CompHour />
        <CompDaily />

      </main>
      <CompMap />
    </AppContext.Provider>
  );
};
export default App;
