import React from 'react';
import { useContext } from 'react';
import { useEffect } from 'react';
import { AppContext } from '../App';
import { fnGetAddress, fnGetCurrentLocation, fnGetWeather } from '../store/function';

const CompHeader = () => {
  let {stChangeLatLng, stChangeAddress, stChangeWeather, fnInit} = useContext(AppContext)

  useEffect(()=>{
    document.querySelector('.btn_map').addEventListener('click', ()=>{
      document.querySelector('.map_section').classList.add('active')
      document.querySelector('.app_container').scrollTo({top:0})
    })
    document.querySelector('.btn_refresh').addEventListener('click', async ()=>{
      let yn = window.confirm('현재위치로 날씨정보를 변경하시겠습니까?')
      if(yn){
        fnInit()
      }
    })
  },[])

  return (
    <header>
      <button className='btn_refresh'><i className="fa-solid fa-rotate-right"></i></button>
      <h1>
        <span className='hidden'>날씨앱</span>
        <img src={`${process.env.PUBLIC_URL}/img/main/title.png`} alt="" />
      </h1>
      <button className='btn_map'><i className="fa-solid fa-map-location-dot"></i></button>
    </header>
  );
};

export default CompHeader;