import React, { useContext } from 'react';
import { AppContext } from '../App';
import CompHourData from './CompHourData';
import 'chart.js/auto';
import { Chart } from 'react-chartjs-2';
import { fnOutput } from '../store/function';
import ChartDataLabels from 'chartjs-plugin-datalabels';

const CompHour = () => {
  let { stWeather } = useContext(AppContext)
  if (!stWeather) return false
  let hourlyArr = stWeather.hourly
  //chart를 도출하는 코딩
  let labelArr = new Array(24).fill('')
  let tempArr = hourlyArr.map((v,i)=>{
    if(i<24){
      let {temp} = fnOutput(v)
      return temp
    }
  })//map
  tempArr.splice(24,24)
  const data = {
    labels: labelArr,
    datasets: [{
      label: 'myGraph',
      data: tempArr,
      borderWidth: 1,
      fill: false,
      borderColor: '#fff',
      tension: 0.2,
    }]
  }
  const options ={
    scales: {
      x: {
        display:false,
        grid:{
          display:false,
        }
      },
      y: {
        display:true,
        beginAtZero: true,
        min: -50,
        max: 50,
        stepSize:10,
        grid:{
          display:true,
          color:'rgba(255,255,255,0.2)'
        },
        ticks:{
          display:false
        }
      },
    },
    plugins: {
      legend: {
        display: false,
        position: 'top',
        labels: {
          color: 'rgb(255, 99, 132)'
        }
      },
      datalabels: {
        display: true,
        color: "white",
        align: 'top',
      }
    },
    responsive: true,
    maintainAspectRatio: false,
  }
  let gridArr = []
  for(let i=5;i>=-5;i--){
    gridArr.push(i*10)
  }
  return (
    <section className='hour_section'>
      <div className='inner scr_style'>
        <h2>
          <i className="fa-regular fa-clock"></i>
          <span>Hourly Forecast</span>
        </h2>
        <div className='hour_container'>
          <ul>
            {
              hourlyArr.map((v, i) => {
                if (i < 24) {
                  return <CompHourData hourData={v} key={i} />
                }
              })//map
            }
          </ul>
          <div className='chart'>
            <Chart type='line' data={data} options={options}  plugins={[ChartDataLabels]}/>
            <div className='grid'>
              {
                gridArr.map((v,i)=>{
                  return <span key={i}>{v}</span>
                })
              }
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default CompHour;